body, html {
  width: 100%;
  height: 100%;
  overflow: hidden;
  margin: 0;
  padding: 0;
  line-height: inherit;
  -webkit-text-size-adjust: none;
  -webkit-font-smoothing: antialiased;
  -webkit-tap-highlight-color: transparent;
  font-family: sans-serif; }

.page-main {
  height: 100%;
  max-height: 100vh;
  width: 100%; }
  .page-main.appear_before {
    opacity: 0; }
  .page-main.appear_after {
    opacity: 1; }

#slider_ui {
  position: fixed;
  bottom: 10vh;
  width: 200px;
  left: calc(50% - 100px);
  display: -ms-flexbox;
  display: flex;
  z-index: 20;
  -ms-flex-pack: stretch;
  justify-content: stretch;
  -ms-flex-align: center;
  align-items: center; }
  #slider_ui button {
    -ms-flex: 1 1 100%;
    flex: 1 1 100%;
    cursor: pointer; }



