body, html {
  width: 100%;
  height: 100%;
  overflow: hidden;
  margin: 0;
  padding: 0;
  line-height: inherit;
  -webkit-text-size-adjust: none;
  -webkit-font-smoothing: antialiased;
  -webkit-tap-highlight-color: transparent;
  font-family: sans-serif; }

.pageTitle {
  position: fixed;
  z-index: 10;
  color: #FFF;
  line-height: 54px; }
  @media (min-width: 2560px) {
    .pageTitle {
      left: calc(50% - 690px);
      top: 160px;
      padding-left: 50px;
      line-height: 75.6px; } }
  @media (min-width: 0) and (max-width: 2560px) {
    .pageTitle {
      left: calc(50% - 690px);
      top: 120px;
      padding-right: 80px;
      padding-left: 50px;
      line-height: 54px; } }
  @media (min-width: 0) and (max-width: 1680px) {
    .pageTitle {
      left: 208.5px;
      top: 120px; } }
  @media (min-width: 0) and (max-width: 1280px) {
    .pageTitle {
      left: 208.5px;
      top: 80px;
      padding-left: 20px; } }
  @media (min-width: 0) and (max-width: 1024px) {
    .pageTitle {
      left: 128.5px;
      top: 40px;
      line-height: 43.2px; } }
  @media (min-width: 0) and (max-width: 640px) {
    .pageTitle {
      opacity: 0 !important; } }



